@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
.header {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 15px 30px;
  cursor: default;
  height: 10vh;
}

.header img {
  height: 48px;
  width: 48px;
}

.header .primaryText {
  font-size: 1.1rem;
  font-weight: bold;
}

.header .secondaryText {
  font-size: 0.75rem;
  font-weight: bold;
  color: #bbbbbd;
}

.header .usernameDateTime {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.header .content {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-left: 1rem;
}

.header .svgContainer {
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 50%;
  padding: calc(36px - 20px);
  display: -webkit-flex;
  display: flex;
  cursor: pointer;
  transition: background-color 0.4s, fill 0.4s;
}

.header .navbar .list {
  list-style-type: none;
  display: -webkit-flex;
  display: flex;
}

.header .navbar .list .item .link {
  text-transform: uppercase;
  font-weight: 600;
  color: #9e9e9e;
  transition: 0.25s color;
  text-decoration: none;
  margin-left: 1.1rem;
  font-size: 1.1rem;
}

.header .navbar .list .item .link:hover {
  color: #000;
}

.dark-theme .header .svgContainer {
	background-color: #000;
	border: 1px solid #fff;
}

.dark-theme .header .primaryText {
	color: #fff;
}

.dark-theme .header .secondaryText,
.dark-theme .header .navbar .list .item .link {
	color: rgba(255, 255, 255, 0.87);
}

main.video-page {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
	-webkit-align-items: center;
	        align-items: center;
	width: 75vw;
	cursor: default;
	max-height: 100%;
	margin: 0;
	margin: initial;
}

.video-page .detail,
.video-page .actions {
	width: 75%;
}

.video-page .detail {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: flex-start;
	        align-items: flex-start;
	height: 100%;
	-webkit-justify-content: space-evenly;
	        justify-content: space-evenly;
}

.video-page .logo {
	height: 2rem;
	width: auto;
	transition: 0.2s;
}

.video-page .logo:hover {
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}

.video-page .detail .detail__title {
	font-size: 2.5rem;
}

.video-page .detail .detail__info {
	margin: 1rem 0;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
}

.video-page .detail .detail__info span {
	margin-right: 0.75rem;
	font-weight: bold;
}

.video-page .detail .detail__about {
	color: #9e9e9e;
	width: 75%;
}

.video-page .actions {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	-webkit-justify-content: space-evenly;
	        justify-content: space-evenly;
	margin-top: 5rem;
}

.video-page .actions .actions__action-container {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	width: 40%;
	padding: 0.75rem 1.25rem 0.75rem 0;
	margin: 1rem 0;
	background-color: #f9f9f9;
	border-radius: 8px;
	cursor: pointer;
	transition: 0.5s;
	font-weight: bold;
	color: #b9bbba;
}

.video-page .actions .actions__action-container:hover {
	box-shadow: 0px 0px 20px 0px rgb(0 0 0 / 19%);
	background-color: #ffffff;
	color: #000;
}

.video-page .actions .actions__action-container svg {
	fill: #b9bbba;
	margin: 0 0.75rem;
	transition: fill 0.5s;
}

.video-page .actions .actions__action-container:hover svg {
	fill: #f00;
}

.aside-image {
	position: fixed;
	right: 13%;
	top: 15%;
	width: auto;
	height: 100%;
	transition: 0.2s;
}

.aside-image:hover {
	-webkit-transform: scale(1.015);
	        transform: scale(1.015);
}

.aside-background {
	background-color: #630404;
	position: absolute;
	top: 0;
	right: 0;
	height: 100vh;
	width: 25vw;
	z-index: -1;
}

.liked {
	color: #f00 !important;
}

.liked svg {
	fill: #f00 !important;
}

.dark-theme .video-page .detail .detail__title {
	color: #fff;
}

.dark-theme .video-page .detail .detail__info span {
	color: #fff;
}

.dark-theme .video-page .detail .detail__about {
	color: rgba(255, 255, 255, 0.87);
}

.dark-theme .header .navbar .list .item .link:hover {
	color: #fff;
}

.dark-theme .video-page .actions .actions__action-container {
	background-color: #000;
	color: rgba(255, 255, 255, 0.87);
}

.dark-theme .video-page .actions .actions__action-container:hover {
	box-shadow: 0px 0px 10px 0px rgb(255, 255, 255, 0.1);
	color: #fff;
}

.dark-theme .aside-background {
  background-color: #2d0000;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	outline: 0;
}

body {
	font: 400 14px 'Montserrat', sans-serif;
	-webkit-font-smoothing: antialiased;
	max-height: 100vh;
}

body.dark-theme {
	background-color: #121212;
}

img {
	max-width: 100%;
	height: auto;
}

.container {
	max-width: 1200px;
  margin: 0 auto;
	padding: 3rem 1rem;
	height: 90vh;
}

